import Node from "./Node";
export default class DeviceNode extends Node {
  constructor({ x, y, width, height }) {
    super();
    this.deviceType = "deviceType";
    this.width = width;
    this.height = height;
    this.borderColor = "#333";
    this.x = x;
    this.y = y;
    console.log(this);
  }
  /**
   * ÈáçÊñ∞ÊèêÁ¨îÁöÑÈÄªËæëÔºåÂè™ÊúâÂú®activeÊÉÖÂÜµÊâçËß¶ÂèëÔºå‰∏çÁÑ∂‰ºöÈôç‰ΩéÊâπÈáèÊ∏≤ÊüìÁöÑÊÄßËÉΩ
   * - Êúâ‰ªÄ‰πàÂäûÊ≥ïÂèØ‰ª•ÂáèÂ∞ëÊèêÁ¨îhover ctxË∞ÉÁî®Ê¨°Êï∞ÈóÆÈ¢ò
   *   - ÈìæË°®Ôºå‰ΩÜÈìæË°®ÁöÑÈÅçÂéÜÈÄüÂ∫¶Â¶Ç‰ΩïÂë¢Ôºü
   *   - jsËøêÁÆóÊàñËÄÖworkerËøêÁÆóÊØîÁõ¥Êé•ctxÊÄßËÉΩÊõ¥È´ò
   *
   * @param {*} ctx
   * @memberof DeviceNode
   */
  render(ctx) {
    ctx.moveTo(this.x, this.y);
    ctx.lineTo(this.width, this.y);
    ctx.lineTo(this.width, this.y + this.height);
    ctx.lineTo(this.x, this.y + this.height);
    ctx.lineTo(this.x, this.y);

    if (this.active) {
      ctx.strokeStyle = "#f80";
      ctx.stroke();
      ctx.beginPath();
    } else {
      ctx.strokeStyle = "#333";
      ctx.stroke();
      ctx.beginPath();
    }
  }
}
